'use client';

import { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { Card } from '@/components/ui/card';
import { Movie, Series, getImageUrl, IMAGE_SIZES } from '@/lib/tmdb-api';
import { FaStar, FaPlay, FaPlus, FaMinus, FaTrash, FaInfoCircle } from 'react-icons/fa';

interface MediaCardProps {
  item: Movie | Series;
  type: 'movie' | 'tv' | 'history' | 'watchlist';
  onAction?: (action: 'play' | 'add' | 'remove' | 'info', item: Movie | Series) => void;
  featured?: boolean;
}

export function MediaCard({ item, type, onAction, featured = false }: MediaCardProps) {
  const [isHovered, setIsHovered] = useState(false);
  const [isImageLoaded, setIsImageLoaded] = useState(false);

  const title = 'title' in item ? item.title : item.name;
  const releaseDate = 'release_date' in item ? item.release_date : item.first_air_date;
  const year = releaseDate ? new Date(releaseDate).getFullYear() : '';
  const rating = item.vote_average.toFixed(1);
  const posterPath = item.poster_path;
  const id = item.id;

  // Route based on item type
  const detailPath = type === 'movie'
    ? `/movie/${id}`
    : type === 'tv'
      ? `/series/${id}`
      : 'title' in item ? `/movie/${id}` : `/series/${id}`;

  const handleAction = (action: 'play' | 'add' | 'remove' | 'info') => {
    if (onAction) {
      onAction(action, item);
    }
  };

  const imageUrl = getImageUrl(posterPath, IMAGE_SIZES.poster.medium);

  return (
    <Card
      className={`overflow-hidden border-none bg-card hover:bg-card-hover transition-all duration-300 rounded-lg ${
        featured ? 'scale-105 shadow-lg' : 'hover:shadow-md'
      }`}
    >
      <div
        className="relative"
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
      >
        {/* New feature flag */}
        {featured && (
          <div className="absolute top-2 left-2 z-20 bg-accent-color text-white px-2 py-1 text-xs rounded-full">
            Featured
          </div>
        )}

        <Link href={detailPath} className="block relative aspect-[2/3] w-full">
          {imageUrl.startsWith('https') ? (
            <img
              src={imageUrl}
              alt={title}
              className={`w-full h-full object-cover rounded-t-lg transition-opacity duration-500 ${
                isImageLoaded ? 'opacity-100' : 'opacity-0'
              }`}
              onLoad={() => setIsImageLoaded(true)}
            />
          ) : (
            <Image
              src={imageUrl}
              alt={title}
              fill
              className={`object-cover rounded-t-lg transition-opacity duration-500 ${
                isImageLoaded ? 'opacity-100' : 'opacity-0'
              }`}
              onLoad={() => setIsImageLoaded(true)}
            />
          )}

          {/* Overlay gradient always present at bottom */}
          <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent opacity-70"></div>

          {/* Hover action overlay */}
          <div
            className={`absolute inset-0 flex items-center justify-center gap-3 bg-black/50 transition-opacity duration-300 ${
              isHovered ? 'opacity-100' : 'opacity-0'
            }`}
          >
            <button
              onClick={(e) => {
                e.preventDefault();
                handleAction('play');
              }}
              className="bg-accent-color hover:bg-hover-color text-white w-10 h-10 rounded-full flex items-center justify-center transition-colors"
              aria-label="Play"
            >
              <FaPlay className="text-sm" />
            </button>

            {(type === 'movie' || type === 'tv') && (
              <button
                onClick={(e) => {
                  e.preventDefault();
                  handleAction('add');
                }}
                className="bg-white/20 hover:bg-white/30 text-white w-10 h-10 rounded-full flex items-center justify-center transition-colors"
                aria-label="Add to watchlist"
              >
                <FaPlus className="text-sm" />
              </button>
            )}

            {type === 'watchlist' && (
              <button
                onClick={(e) => {
                  e.preventDefault();
                  handleAction('remove');
                }}
                className="bg-white/20 hover:bg-white/30 text-white w-10 h-10 rounded-full flex items-center justify-center transition-colors"
                aria-label="Remove from watchlist"
              >
                <FaMinus className="text-sm" />
              </button>
            )}

            {type === 'history' && (
              <button
                onClick={(e) => {
                  e.preventDefault();
                  handleAction('remove');
                }}
                className="bg-white/20 hover:bg-white/30 text-white w-10 h-10 rounded-full flex items-center justify-center transition-colors"
                aria-label="Remove from history"
              >
                <FaTrash className="text-sm" />
              </button>
            )}
          </div>

          {/* Rating badge */}
          <div className="absolute bottom-2 left-2 flex items-center text-xs text-white">
            <FaStar className="text-rating-color mr-1" />
            <span>{rating}</span>
          </div>

          {/* Year badge */}
          <div className="absolute bottom-2 right-2 text-xs text-white/80">
            {year}
          </div>
        </Link>

        {/* Title */}
        <div className="p-2">
          <h3 className="font-medium text-sm truncate" title={title}>
            {title}
          </h3>
        </div>
      </div>
    </Card>
  );
}
